import {eId, eClass, darkLightMode} from '../../components/helpers/StylingHelpers';

function CreateCourseStyle(){
    setDarkLightMode();
    setStyle();

    var userAgent = navigator.userAgent || navigator.vendor || window.opera;
    if (!(/android/i.test(userAgent))) {
        window.addEventListener('resize', setStyle);
    }
}

export default CreateCourseStyle;

function setDarkLightMode(){
    darkLightMode("newCourse");
    darkLightMode('input');
    darkLightMode('button');
}

function setStyle(){
    if(!eId('newCourse')){
        return false;
    }
    
    eId("newCourse").style.minHeight = eId('newCourse').offsetWidth * 0.6 + 'px';

    if(window.innerWidth > window.innerHeight){
        eId('newCourse').style.borderRadius = parseInt(eId('newCourse').offsetHeight) * 0.07 + 'px';
    } else {
        eId('newCourse').style.borderRadius = parseInt(eId('newCourse').offsetWidth) * 0.07 + 'px';
    }

    if(eId('newCourse').offsetWidth > eId('newCourse').offsetHeight){
        eId("newCourseForm").className = eId("newCourseForm").className.replace(" mb-5", "") + " mb-3";
    } else {
        eId("newCourseForm").className = eId("newCourseForm").className.replace(" mb-1","") + " mb-1";
    }

    eId("newCourseForm").className = eId("newCourseForm").className.replace(" card-body","") + " card-body";

    setInput('input');
    setBtn('btn');
}

function setInput(ref){
    let input = eClass(ref);
    for(let i = 0; i < input.length; i++){
        input[i].style.borderRadius = input[0].offsetWidth * 0.025 + 'px';
    }
}

function setBtn(ref){
    let btn = eClass(ref);
    for(let i = 0; i < btn.length; i++){
        btn[i].style.borderRadius = btn[0].offsetWidth * 0.15 + 'px';
    }
}